.example {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  background-color: #fff;
  color: #424242;
  max-width: 960px;
  padding: 16px;
}

.example code {
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
  background-color: #009688;
  color: #fff;
  padding: 2px 6px;
  border-radius: 2px;
}

.example .background {
  height: 240px;
  // Путь к изображениям из SASS-файла всегда такой, как из папки css
  background-image: url('../images/demo.jpg');
  background-size: cover;
  background-position: center;
}

.winner, .progress{
  padding-left: 22px;
  min-height: 22px;
}
.winner{
  color: red;
}
.progress{
  color: brown;
}
.table{
  padding-left: 44px;
}
.start-btn{
  width: 55px;
  height: 55px;
  border: solid 2px rgb(15, 235, 15);
  border-radius: 50%;
  margin-left: 75px;
}